<template>
  <v-card
    max-width="100%"
    height="100%"
  >
    <v-toolbar
      color="pink"
      dark
      class="px-4"
      tile
      style="position: fixed; z-index: 8; width: 100%;"
    >
      <v-app-bar-nav-icon @click.stop="showDrawer"></v-app-bar-nav-icon>

      <v-toolbar-title>Mi Perfil</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn text @click="updateProfile">Guardar</v-btn>
    </v-toolbar>
    <v-sheet
      id="scrolling-techniques-7"
      class="overflow-y-auto"
      max-height="100vh"
    >
      <v-container id="profile-container" class="pt-16">

          <!-- <Breadcrumbs /> -->

          <ValidationObserver ref="observer">
            <form autocomplete="off" @submit.prevent="updateProfile">

                  <h4 class="text-bolder px-3 pt-7 pb-0">Información básica</h4>
                  <!-- Nombre y apellidos -->
                  <v-row justify="space-between">
                    <v-col cols="12" class="pb-1">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="firstname"
                          rules="required"
                        >
                        <!-- <label for="name">Nombre*</label> -->
                        <v-text-field
                          id="firstname"
                          v-model="user.firstname"
                          label="Nombre *"
                          placeholder="Nombre completo"
                          outlined
                          dense
                          class="mx-auto rounded-lg"
                          :error-messages="errors"
                          :hide-details="errors.length > 0 ? false : true"
                        ></v-text-field>
                      </ValidationProvider>
                    </v-col>
                    <v-col cols="12" class="pb-1">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="lastname"
                          rules="required"
                        >
                        <!-- <label for="lastName">Apellidos*</label> -->
                      <v-text-field
                        id="lastname"
                        v-model="user.lastname"
                        label="Apellidos *"
                        placeholder="Apellidos completo"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>

                  <!-- Correo y movil -->
                    <v-col cols="12" class="pb-1">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="email"
                          rules="required"
                        >
                        <!-- <label for="email">Correo electrónio*</label> -->
                        <v-text-field
                          id="email"
                          v-model="user.email"
                          label="Correo electrónio *"
                          placeholder="Escriba su correo"
                          outlined
                          dense
                          class="mx-auto rounded-lg"
                          :error-messages="errors"
                          :hide-details="errors.length > 0 ? false : true"
                        ></v-text-field>
                      </ValidationProvider>
                    </v-col>

                  </v-row>

                  <h4 class="text-bolder px-3 pt-7 pb-0">Cambiar contraseña</h4>
                  <v-row justify="space-between">
                    <v-col cols="12" md="4" sm="4" xs="12">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="oldPassword"
                          :rules="newPassword !== null && newPassword !== '' ? 'required|min:8' : 'min:8'"
                        >
                      <v-text-field
                        ref="oldPassword"
                        id="oldPassword"
                        v-model="oldPassword"
                        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show1 ? 'text' : 'password'"
                        placeholder="Contraseña actual"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                        @click:append="show1 = !show1"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>
                    <v-col cols="12" md="4" sm="4" xs="12">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="password"
                          :rules="oldPassword != null && oldPassword != '' ? 'required|min:8' : 'min:8'"
                        >
                      <v-text-field
                        ref="password"
                        id="password"
                        v-model="newPassword"
                        :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show2 ? 'text' : 'password'"
                        placeholder="Nueva contraseña"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                        @click:append="show2 = !show2"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>
                    <v-col cols="12" md="4" sm="4" xs="12">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="password_confirm"
                          :rules="newPassword != null && newPassword != '' ? 'required|min:8|confirmed:password' : 'confirmed:password'"
                        >
                      <v-text-field
                        id="password_confirm"
                        v-model="passwordConfirm"
                        :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show3 ? 'text' : 'password'"
                        placeholder="Confirmar nueva contraseña"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                        @click:append="show3 = !show3"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>
                  </v-row>

            </form>
          </ValidationObserver>
        </v-container>
    </v-sheet>
  </v-card>
</template>

<script>
import userServices from '@/axios/user.services'
import states from '@/nomencladores/states.json'

export default {
  name: 'Profile',
  components: {
  },
  data: function () {
    return {
      user: JSON.parse(localStorage.getItem('store_user')).user,
      oldPassword: null,
      newPassword: null,
      passwordConfirm: null,
      show1: false,
      show2: false,
      show3: false,
      roles: [],
      genders: [{ id: 'F', text: 'Femenino' }, { id: 'M', text: 'Masculino' }]
    }
  },
  created () {
  },
  mounted () {
    for (const key in states) {
      this.states.push(key)
    }
  },
  watch: {
  },
  computed: {
  },
  methods: {
    showDrawer () {
      this.$store.dispatch('showDrawer', !this.$store.getters.showDrawer())
    },
    updateProfile () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          // delete this.user.profile.telegram
          const id = this.user._id
          delete this.user._id
          userServices.updateUser(id, this.user).then((response) => {
            if (response) {
              if (this.oldPassword != null && this.passwordConfirm != null) {
                userServices.updateUserPassword({
                  oldPassword: this.oldPassword,
                  newPassword: this.passwordConfirm
                }).then((changeResponse) => {
                  if (changeResponse) {
                    this.$toast.success('Perfil actualizado!')
                  }
                }).catch(() => {
                  this.$toast.error('Error al actualizar contraseña')
                })
              } else {
                this.$toast.success('Perfil actualizado!')
              }

              var cubamaxUser = JSON.parse(localStorage.getItem('store_user'))
              cubamaxUser.user = this.user
              localStorage.setItem('store_user', JSON.stringify(cubamaxUser))
            }
          }).catch(() => {
            this.$toast.error('Error al actualizar perfil')
          })
        }
      })
    },
    cancel () {
      this.$emit('cancel')
    }
  }
}
</script>

<style lang="scss" scoped>
.row{
  margin: 7px 0 !important;
}
h3{
  text-transform: uppercase;
}

</style>
