const mutations = {

  logOut (state) {
    state.userIsLogged = false
    state.userIsAdmin = false
    state.userInfo = null
    localStorage.clear()
  },

  changeUserAdminStatus (state, status) {
    state.userIsAdmin = status
  },

  logIn (state, info) {
    state.userInfo = info
    state.userIsLogged = true
    state.userIsAdmin = info.user.role.name === 'Admin'
    localStorage.setItem('store_user', JSON.stringify(info))
  },

  updateUserInformation (state, info) {
    state.userInfo = info
    state.userIsAdmin = info.user.role.name === 'Admin'
    localStorage.setItem('store_user', JSON.stringify(info))
  },

  loadLocalUserInformation () {
    if (localStorage.getItem('store_user')) {
      if (JSON.parse(localStorage.getItem('store_user')).user.role && JSON.parse(localStorage.getItem('store_user')).user.role.name === 'Admin') {
        state.userIsAdmin = true
      }
      state.userIsLogged = true
    }
  },

  showDrawer (state, status) {
    state.drawer = status
  },

  addEvent (state, event) {
    state.events.push(event)
  },

  addMenu (state, event) {
    state.catalog.push(event)
  }
}

const actions = {

  logOut ({ commit }, status) {
    commit('logOut', status)
  },

  changeUserAdminStatus ({ commit }, status) {
    commit('changeUserAdminStatus', status)
  },

  logIn ({ commit }, info) {
    commit('logIn', info)
  },

  updateUserInformation ({ commit }, info) {
    commit('updateUserInformation', info)
  },

  loadLocalUserInformation ({ commit }) {
    commit('loadLocalUserInformation')
  },

  showDrawer ({ commit }, status) {
    commit('showDrawer', status)
  },

  addEvent ({ commit }, event) {
    commit('addEvent', event)
  },

  addMenu ({ commit }, event) {
    commit('addMenu', event)
  }
}

const getters = {
  userIsLogged: (state) => () => {
    return state.userIsLogged
  },
  isAdmin: (state) => () => {
    return state.userIsAdmin
  },
  userInfo: (state) => () => {
    return state.userInfo
  },
  showDrawer: (state) => () => {
    return state.drawer
  },
  events: (state) => () => {
    return state.events
  },
  catalog: (state) => () => {
    return state.catalog
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
