<template>
  <v-card class="overflow-hidden elevation-0 rounded-0">
    <v-app-bar
      absolute
      scroll-target="#scrolling-techniques-7"
      color="pink"
      dark
      class="px-4"
      tile
    >
      <v-app-bar-nav-icon @click.stop="showDrawer"></v-app-bar-nav-icon>

      <v-toolbar-title>Ganancias</v-toolbar-title>

      <v-spacer></v-spacer>
    </v-app-bar>

    <v-tabs
      v-model="tab"
      centered
      icons-and-text
      height="37"
      grow
      class="mt-15"
    >
      <v-tabs-slider></v-tabs-slider>

      <v-tab href="#today">
        Día
      </v-tab>
      <v-tab href="#week">
        Semana
      </v-tab>
      <v-tab href="#month">
        Mes
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item value="today" class="mt-16">

        <v-row justify="center">
          <v-col class="pb-0" cols="auto">
            <label for="">2570.00 CUP</label>
          </v-col>
          <v-col class="pb-0" cols="auto">
            <label for="">7000.00 USD</label>
          </v-col>
          <v-col cols="auto">
            <label for="">7000.00 MLC</label>
          </v-col>
        </v-row>

        <v-row>
          <v-col>
            <v-btn dark color="pink" block class="mt-16" @click="goToDetails">
              Ver detalles
            </v-btn>
          </v-col>
        </v-row>
      </v-tab-item>

      <v-tab-item value="week" class="mt-16">

        <v-row justify="center">
          <v-col class="pb-0" cols="auto">
            <label for="">570.00 CUP</label>
          </v-col>
        </v-row>

        <v-row>
          <v-col>
            <v-btn dark color="pink" block class="mt-16" @click="goToDetails">
              Ver detalles
            </v-btn>
          </v-col>
        </v-row>
      </v-tab-item>

      <v-tab-item value="month" class="mt-16">

        <v-row justify="center">
          <v-col class="pb-0" cols="auto">
            <label for="">1570.00 CUP</label>
          </v-col>
        </v-row>

        <v-row>
          <v-col>
            <v-btn dark color="pink" block class="mt-16" @click="goToDetails">
              Ver detalles
            </v-btn>
          </v-col>
        </v-row>
      </v-tab-item>
    </v-tabs-items>

  </v-card>
</template>

<script>
export default {
  name: 'Report',
  data: () => ({
    search: {
      date: null,
      product: null,
      provider: null
    },
    tab: null,
    selected: [],
    selecting: false,
    interval: false,
    count: 0,
    catalog: [],
    providers: []
  }),
  created () {
    this.catalog = this.$store.getters.catalog()
    this.providers = this.$store.getters.providers()
  },
  watch: {
    selected (newValue, oldValue) {
      if (newValue.length === 0) {
        this.selecting = false
      }
    },
    tab (newValue, oldValue) {
      this.search.date = newValue
      this.tab = newValue
    }
  },
  methods: {
    start (row) {
      this.interval = setInterval(() => {
        this.selecting = true
        // this.selected.push(row)
        return this.selecting
      }, 400)
    },
    stop () {
      clearInterval(this.interval)
      this.interval = false
    },
    showDrawer () {
      this.$store.dispatch('showDrawer', !this.$store.getters.showDrawer())
    },
    clearSelection () {
      if (!this.selecting) {
        this.selected = []
      }
    },
    editProduct (index) {
      this.$router.push({ name: 'edit-menu', params: { menu: this.catalog[this.selected[0]] } })
    },
    deleteProduct () {
      this.selected.forEach(element => {
        this.catalog = this.catalog.filter(x => x.name !== element)
      })
    },
    goToDetails (index) {
      this.$router.push({ name: 'report-details', params: { search: this.search } })
    }
  }
}
</script>

<style lang="scss">
.catalog .v-list-item__subtitle {
  white-space: normal !important;
}
.f-12 {
  font-size: 13px !important;
}
.f-13 {
  font-size: 13px !important;
}
.square-avatar {
  border-radius: 0 !important;
}
span.v-ripple_container {
  display: none !important;
}
</style>
