import Vue from 'vue'
import VueRouter from 'vue-router'

import Profile from '../views/profile'

import Start from '../views/authentication/Start.vue'
import Login from '../views/authentication/Login.vue'
import Signup from '../views/authentication/Signup.vue'
import Confirmation from '../views/authentication/Confirmation.vue'
import ForgotPassword from '../views/authentication/ForgotPassword.vue'
import Error404 from '../views/404/404.vue'
import MyStore from '@/views/store'
import StoreForm from '@/views/store/StoreForm.vue'
import Products from '@/views/products'
import ProductForm from '@/views/products/ProductForm.vue'
import Report from '@/views/report'
import ReportDetailts from '@/views/report/ReportDetails.vue'
import Calendar from '@/views/calendar'
import SalePromotionForm from '@/views/calendar/SalePromotionForm'
import Providers from '@/views/providers'
import ProviderForm from '@/views/providers/ProviderForm'

Vue.use(VueRouter)

const isLogged = (to, from, next) => {
  if (!localStorage.getItem('store_user')) {
    router.replace('/start')
  } else {
    next()
  }
}

const isOwner = (to, from, next) => {
  if (localStorage.getItem('store_user')) {
    if (JSON.parse(localStorage.getItem('store_user')).user.role &&
      (JSON.parse(localStorage.getItem('store_user')).user.role.name === 'Owner' ||
      JSON.parse(localStorage.getItem('store_user')).user.role.name === 'Businessman')) {
      next()
    }
  } else {
    router.replace('/start')
  }
}

