const routes = [
  {
    path: '/', redirect: 'my-store', beforeEnter: isLogged
  },
  {
    path: '/start', name: 'start', component: Start
  },
  {
    path: '/login', name: 'login', component: Login
  },
  {
    path: '/registrarse', name: 'signup', component: Signup
  },
  {
    path: '/confirmar', name: 'confirmation', component: Confirmation
  },
  {
    path: '/recuperar-clave', name: 'forgotPassword', component: ForgotPassword
  },
  // {
  //   path: '/logout', name: 'logout', component: RestaurantHome, beforeEnter: loguot
  // },
  {
    path: '/error/404', name: '404', component: Error404
  },
  { path: '/my-store', name: 'my-store', component: MyStore, beforeEnter: isOwner },
  { path: '/my-store/create', name: 'create-store', component: StoreForm, beforeEnter: isOwner },
  { path: '/my-store/edit/:id', name: 'edit-store', component: StoreForm, beforeEnter: isOwner },
  { path: '/store-products', name: 'products', component: Products, beforeEnter: isOwner },
  { path: '/store-products/create', name: 'create-product', component: ProductForm, beforeEnter: isOwner },
  { path: '/store-products/edit/:id', name: 'edit-product', component: ProductForm, beforeEnter: isOwner },
  { path: '/providers', name: 'providers', component: Providers, beforeEnter: isOwner },
  { path: '/providers/create-provider', name: 'create-provider', component: ProviderForm, beforeEnter: isOwner },
  { path: '/providers/edit-provider/:id', name: 'edit-provider', component: ProviderForm, beforeEnter: isOwner },
  { path: '/calendar', name: 'calendar', component: Calendar, beforeEnter: isOwner },
  { path: '/calendar/sale-promotion', name: 'sale-promotion', component: SalePromotionForm, beforeEnter: isOwner },
  { path: '/report', name: 'report', component: Report, beforeEnter: isOwner },
  { path: '/report/details', name: 'report-details', component: ReportDetailts, beforeEnter: isOwner },
  { path: '/profile', name: 'profile', component: Profile, beforeEnter: isOwner }

]

const scrollBehavior = (to, from, savedPosition) => {
  if (savedPosition) {
    return savedPosition
  }
  if (to.hash) {
    return { selector: to.hash }
  }
  return { x: 0, y: 0 }
}
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior
})

export default router
