<template>
  <v-card class="overflow-hidden elevation-0 rounded-0">
    <v-toolbar
      color="pink"
      dark
      tile
      style="position: fixed; z-index: 8; width: 100%;"
    >
        <back-button />

        <v-toolbar-title>{{ currentTitle }}</v-toolbar-title>

        <v-spacer></v-spacer>

    </v-toolbar>

    <v-row class="mt-16">
      <v-col cols="12" class="pb-1">
        <v-text-field
          ref="product"
          id="product"
          v-model="search.product"
          label="Nombre del producto"
          outlined
          dense
          class="mx-auto rounded-lg"
          hide-details
        >
        </v-text-field>
      </v-col>
      <v-col cols="12" class="pb-1">
        <v-combobox
          v-model="search.provider"
          :items="providers"
          item-value="id"
          item-text="name"
          clearable
          outlined
          dense
          label="Proveedor"
          hide-details=""
          class="mx-auto rounded-lg"
        ></v-combobox>
      </v-col>
    </v-row>

    <v-divider class="my-3"></v-divider>

    <v-sheet
      id="scrolling-techniques-7"
      class="overflow-y-auto"
      max-height="68.5vh"
    >
      <v-container class="pa-0">

          <v-list two-line class="catalog pt-0">
            <template v-for="(item, index) in catalog">
              <v-list-item :key="index" :ripple="false">
                <template v-slot:default>
                  <v-list-item-avatar height="auto" class="square-avatar">
                    <v-img src="../../assets/1.jpg" lazy-src="../../assets/1.jpg"></v-img>
                  </v-list-item-avatar>
                  <v-list-item-content>
                    <v-list-item-title v-text="item.name"></v-list-item-title>
                    <v-list-item-subtitle class="f-13"><span class="text-bold">Proveedor:</span> Ana Maria</v-list-item-subtitle>
                    <v-list-item-subtitle class="f-13"><span class="text-bold">Precio producto:</span> 1500.00 CUP</v-list-item-subtitle>
                    <v-list-item-subtitle class="f-13"><span class="text-bold">Precio venta:</span> 2000.00 CUP</v-list-item-subtitle>
                    <v-list-item-subtitle class="f-13"><span class="text-bold">Ganancia:</span> 500.00 CUP</v-list-item-subtitle>
                  </v-list-item-content>

                  <v-list-item-action class="align-self-baseline">
                    <v-list-item-action-text>15/01/2022</v-list-item-action-text>
                  </v-list-item-action>
                </template>
              </v-list-item>

              <v-divider
                v-if="index < catalog.length - 1"
                :key="'divider_'+index"
              ></v-divider>
            </template>
        </v-list>

      </v-container>
    </v-sheet>
  </v-card>
</template>

<script>
import BackButton from '../../components/BackButton.vue'
export default {
  components: { BackButton },
  name: 'ReportDetails',
  data: () => ({
    search: null,
    title: null,
    selected: [],
    selecting: false,
    interval: false,
    count: 0,
    catalog: [],
    providers: []
  }),
  created () {
    this.catalog = this.$store.getters.catalog()
    this.providers = this.$store.getters.providers()
    this.search = this.$route.params.search
  },
  computed: {
    currentTitle () {
      if (this.search.date) {
        switch (this.search.date) {
          case 'today': return 'Ganancias del día'
          case 'week': return 'Ganancias de la semana'
          case 'month': return 'Ganancias del mes'
          default: return 'Detalles'
        }
      }
    }
  },
  watch: {
  },
  methods: {
    start () {
      this.interval = setInterval(() => {
        this.selecting = true
        return this.selecting
      }, 400)
    },
    stop () {
      clearInterval(this.interval)
      this.interval = false
    },
    showDrawer () {
      this.$store.dispatch('showDrawer', !this.$store.getters.showDrawer())
    },
    clearSelection () {
      if (!this.selecting) {
        this.selected = []
      }
    },
    editProduct (index) {
      this.$router.push({ name: 'edit-menu', params: { menu: this.catalog[this.selected[0]] } })
    },
    deleteProduct () {
      this.selected.forEach(element => {
        this.catalog = this.catalog.filter(x => x.name !== element)
      })
    }
  }
}
</script>

<style lang="scss">
.catalog .v-list-item__subtitle {
  white-space: normal !important;
}
.f-12 {
  font-size: 13px !important;
}
.f-13 {
  font-size: 13px !important;
}
.square-avatar {
  border-radius: 0 !important;
}
span.v-ripple_container {
  display: none !important;
}
</style>
