<template>
  <v-main>
    <v-toolbar
      color="pink"
      dark
      tile
      style="position: fixed; z-index: 8; width: 100%;"
    >
      <back-button />

      <v-toolbar-title>{{ formTitle }}</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn text @click="save">Guardar</v-btn>
    </v-toolbar>
    <v-container class="mt-16">

        <ValidationObserver ref="observer">
          <form autocomplete="off" @submit.prevent="updateProfile">

                <!-- Nombre y apellidos -->
                <v-row justify="space-between">
                  <v-col cols="12" class="pb-1">
                    <ValidationProvider
                        v-slot="{ errors }"
                        name="name"
                        rules="required"
                      >
                      <v-text-field
                        id="name"
                        v-model="editedItem.name"
                        label="Nombre *"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>

                  <v-col cols="12" class="pb-1">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="phone"
                      rules="required|min:8|numeric"
                    >
                      <v-text-field
                        ref="phone"
                        id="phone"
                        v-model="editedItem.phone"
                        label="Teléfono *"
                        placeholder="Teléfono del local"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      >
                      </v-text-field>
                    </ValidationProvider>
                  </v-col>
                </v-row>

          </form>
        </ValidationObserver>
    </v-container>
  </v-main>
</template>

<script>
// import userServices from '@/axios/user.services'
import BackButton from '@/components/BackButton.vue'
// import moment from 'moment'
// import provinces from '@/nomencladores/provinces.json'

export default {
  name: 'ProviderForm',
  components: {
    BackButton
  },
  data: function () {
    return {
      editedItem: {
        name: null,
        phone: null
      },
      formTitle: 'Nuevo proveedor'
    }
  },
  created () {
    if (this.$route.params.provider) {
      this.editedItem = this.$route.params.provider
      this.formTitle = 'Editar proveedor'
    }
  },
  mounted () {

  },
  watch: {
  },
  computed: {
  },
  methods: {
    save () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          // delete this.user.profile.telegram
          this.$store.dispatch('addProvider', this.editedItem)
          this.$router.push('/providers')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.row{
  margin: 7px 0 !important;
}
h3{
  text-transform: uppercase;
}
.inner-select {
  max-width: fit-content !important;
  padding-top: 0 !important;
  margin-top: 0 !important;
}
</style>

<style lang="scss">
.currency-selector .v-input__append-inner {
  margin-top: 5px !important;

  .v-input__control > .v-input__slot:before {
    border: none;
  }
}
</style>
