<template>
  <v-navigation-drawer
    v-model="showDrawer"
    absolute
    left
    temporary
    width="55%"
    style="position: fixed; top: 55px; padding-top: 25px"
    color="pink lighten-5"
    >

    <v-list dense>

      <v-list-item-group
        v-model="group"
      >
        <v-list-item
          v-for="item in items"
          :key="item.title"
          :style="show(item) ? 'display: block' : 'display: none'"
          :to="item.url"
        >
          <template v-slot:default>
            <!-- <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon> -->

            <v-divider class="my-5" v-if="item.divider"></v-divider>

            <v-list-item-content>
              <v-list-item-title class="text-bolder">{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </template>
        </v-list-item>

        <v-list-item style="display: block;" v-if="isLogged" @click="logout">
          <template v-slot:default>
            <v-divider class="my-5"></v-divider>
            <v-list-item-content>
              <v-list-item-title class="text-bolder">
                Salir
              </v-list-item-title>
            </v-list-item-content>
          </template>
        </v-list-item>

      </v-list-item-group>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import userServices from '@/axios/user.services'
import router from '../../router'

export default {
  name: 'Settings',
  data () {
    return {
      dialogDelete: false,
      // userRol: JSON.parse(localStorage.getItem('store_user')).user.role.name,
      userRol: 'Admin',
      items: [
        { title: 'Mi Venta', icon: 'mdi-home-city', url: '/my-store', roles: ['Admin', 'Owner', 'Businessman'] },
        { title: 'Calendario', icon: 'mdi-home-city', url: '/calendar', roles: ['Admin', 'Owner', 'Businessman'] },
        { title: 'Productos', icon: 'mdi-home-city', url: '/store-products', roles: ['Admin', 'Owner', 'Businessman'] },
        { title: 'Proveedores', icon: 'mdi-home-city', url: '/providers', roles: ['Admin', 'Owner', 'Businessman'] },
        { title: 'Ganancia', icon: 'mdi-home-city', url: '/report', roles: ['Admin', 'Owner', 'Businessman'] },
        { title: 'Mi Perfil', divider: true, icon: 'mdi-home-city', url: '/profile', roles: ['Admin', 'Owner', 'Businessman'] }
      ],
      group: null,
      drawer: false,
      sizes: {
        contentWidth: '70%',
        height: 'auto !important'
      }
    }
  },
  watch: {
    // '$route' (to, from) {
    //   this.$store.dispatch('showDrawer', false)
    // },
    group () {
      this.$store.dispatch('showDrawer', false)
    }
  },

  computed: {
    notificationAmoutn: {
      get () {
        return this.$store.getters.cart().length
      }
    },

    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': {
          this.drawer = false
          this.sizes.contentWidth = '100%'
          this.sizes.height = '100% !important'
          return true
        }
        case 'sm': {
          this.drawer = false
          this.sizes.contentWidth = '90%'
          this.sizes.height = '100% !important'
          return true
        }
        case 'md': {
          this.drawer = true
          this.sizes.contentWidth = '70%'
          this.sizes.height = 'auto !important'
          return false
        }
        case 'lg': {
          this.drawer = true
          this.sizes.contentWidth = '70%'
          this.sizes.height = 'auto !important'
          return false
        }
        case 'xl': {
          this.drawer = true
          this.sizes.contentWidth = '70%'
          this.sizes.height = 'auto !important'
          return false
        }
      }
    },
    isAdmin () {
      return this.$store.getters.isAdmin()
    },
    showDrawer () {
      return this.$store.getters.showDrawer()
    },
    isLogged () {
      return this.$store.getters.userIsLogged()
    }
  },
  methods: {
    show (item) {
      if (item.roles.length > 0) {
        return item.roles.indexOf(this.userRol) !== -1
      } else {
        return this.$store.getters.userIsLogged()
      }
    },

    deleteAccountConfirm () {
      userServices.deleteMyAccount().then((result) => {
        if (result) {
          this.$store.dispatch('logOut')
          this.$store.dispatch('clearCart')
          router.replace('/')
          this.isLogged = false
        }
      }).catch(() => {
        this.$toast.error('No se pudo eliminar el storee')
      })
    },

    closeDelete () {
      this.dialogDelete = false
    },

    logout () {
      this.$store.dispatch('logOut')
      this.$store.dispatch('clearCart')
      router.replace('/start')
      this.isLogged = false
    }
  }
}
</script>

<style lang="scss" scoped>
.v-navigation-drawer{
  height: 100% !important;
}
.row{
  margin: 0;
}
.active-route{
  background: rgba(48,153,153,1);
  color: white !important;
  box-shadow: 1px 1px 32px rgba(48, 153, 153, 0.322);
}
.v-list-item__title {
  font-size: 16px !important;
}
</style>
