<template>
  <v-card
    max-width="100%"
    height="100%"
  >
    <v-toolbar
      color="pink"
      dark
      class="px-4"
      tile
      style="position: fixed; z-index: 8; width: 100%;"
      v-if="!selected.length"
    >
      <v-app-bar-nav-icon @click.stop="showDrawer"></v-app-bar-nav-icon>

      <v-toolbar-title>Lista de productos</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon rounded @click="createProduct">
        <v-icon class="mr-2">mdi-plus</v-icon>
      </v-btn>
    </v-toolbar>

    <v-toolbar
      color="pink"
      dark
      class="px-4"
      tile
      style="position: fixed; z-index: 8; width: 100%;"
      v-if="selected.length"
    >
      <v-btn icon @click="clear" v-if="selected.length >= 1">
        <v-icon>mdi-close</v-icon>
      </v-btn>

      <v-toolbar-title class="pl-0">{{ `${selected.length} seleccionados`}}</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn class="ml-2 pr-0" text v-if="selected.length == 1" @click="editProduct">
        <v-icon>mdi-pencil</v-icon>
      </v-btn>
      <v-btn class="px-0" text v-if="selected.length == 1" @click="deleteProduct">
        <v-icon>mdi-delete</v-icon>
      </v-btn>

      <v-btn text v-if="selected.length > 1" @click="deleteProduct">
        Borrar
        <v-icon class="ml-2">mdi-delete</v-icon>
      </v-btn>
    </v-toolbar>

    <v-list two-line class="mt-12 catalog">
      <v-list-item-group
        v-model="selected"
        active-class="pink--text"
        multiple
        @change="clearSelection"
      >
      <template>
          <v-list-item :ripple="false" :value="1"
            @mousedown="start(1)"
            @mouseleave="stop"
            @mouseup="stop"
            @touchstart="start(1)"
            @touchend="stop"
            @touchcancel="stop">
            <template v-slot:default>
              <v-list-item-avatar height="auto" class="square-avatar" @click="goToDetails">
                <v-img src="../../assets/1.jpg" lazy-src="../../assets/1.jpg"></v-img>
              </v-list-item-avatar>
              <v-list-item-content @click="goToDetails">
                <v-list-item-title>Pantalon rojo de meclilla</v-list-item-title>
                <v-list-item-subtitle class="f-13"><span class="text-bold">Proveedor:</span> Ana Maria</v-list-item-subtitle>
                <v-list-item-subtitle class="f-13"><span class="text-bold">Precio venta:</span> 1500.00 CUP</v-list-item-subtitle>
                <v-list-item-subtitle class="f-13"><span class="text-bold">Ganancia:</span> 250.00 CUP</v-list-item-subtitle>
              </v-list-item-content>

              <v-list-item-action>
                <v-col class="pa-0 mb-3 text-center">
                    <p class="mb-1 f-12">Pendiente</p>
                    <v-switch
                      :disabled="selecting"
                      hide-details
                    >
                    </v-switch>
                </v-col>
                <v-col class="pa-0 text-center">
                    <p class="mb-1 f-12">Vendido</p>
                    <v-switch
                      :disabled="selecting"
                      hide-details
                    >
                    </v-switch>
                </v-col>
              </v-list-item-action>
            </template>
          </v-list-item>

          <v-list-item :ripple="false" :value="2"
            @mousedown="start(2)"
            @mouseleave="stop"
            @mouseup="stop"
            @touchstart="start(2)"
            @touchend="stop"
            @touchcancel="stop">
            <template v-slot:default>
              <v-list-item-avatar height="auto" class="square-avatar" @click="goToDetails">
                <v-img src="../../assets/5.jpg" lazy-src="../../assets/5.jpg"></v-img>
              </v-list-item-avatar>
              <v-list-item-content @click="goToDetails">
                <v-list-item-title>Pantalon rojo de meclilla</v-list-item-title>
                <v-list-item-subtitle class="f-13"><span class="text-bold">Proveedor:</span> Ana Maria</v-list-item-subtitle>
                <v-list-item-subtitle class="f-13"><span class="text-bold">Precio venta:</span> 1500.00 CUP</v-list-item-subtitle>
                <v-list-item-subtitle class="f-13"><span class="text-bold">Ganancia:</span> 250.00 CUP</v-list-item-subtitle>
              </v-list-item-content>

              <v-list-item-action>
                <v-col class="pa-0 mb-3 text-center">
                    <p class="mb-1 f-12">Pendiente</p>
                    <v-switch
                      :disabled="selecting"
                      hide-details
                    >
                    </v-switch>
                </v-col>
                <v-col class="pa-0 text-center">
                    <p class="mb-1 f-12">Vendido</p>
                    <v-switch
                      :disabled="selecting"
                      hide-details
                    >
                    </v-switch>
                </v-col>
              </v-list-item-action>
            </template>
          </v-list-item>
          <v-divider
          ></v-divider>
        </template>
        <!-- <template v-for="(item, index) in catalog">
          <v-list-item :key="item.name" :value="item.name">
            <template v-slot:default>
              <v-list-item-content>
                <v-list-item-title v-text="item.name"></v-list-item-title>
                <v-list-item-subtitle v-text="item.description"></v-list-item-subtitle>
              </v-list-item-content>

              <v-list-item-action>
                <v-list-item-action-text v-text="`${item.price} ${item.currency}`"></v-list-item-action-text>
              </v-list-item-action>
            </template>
          </v-list-item>

          <v-divider
            v-if="index < catalog.length - 1"
            :key="index"
          ></v-divider>
        </template> -->
      </v-list-item-group>
    </v-list>
  </v-card>
</template>

<script>
export default {
  name: 'Report',
  data: () => ({
    tab: null,
    selected: [],
    selecting: false,
    catalog: [],
    interval: false,
    count: 0
  }),
  created () {
    this.catalog = this.$store.getters.catalog()
  },
  watch: {
    selected (newValue, oldValue) {
      if (newValue.length === 0) {
        this.selecting = false
      }
    }
  },
  methods: {
    start (row) {
      this.interval = setInterval(() => {
        this.selecting = true
        // this.selected.push(row)
        return this.selecting
      }, 350)
    },
    stop () {
      clearInterval(this.interval)
      this.interval = false
    },
    showDrawer () {
      this.$store.dispatch('showDrawer', !this.$store.getters.showDrawer())
    },
    clearSelection () {
      if (!this.selecting) {
        this.selected = []
      }
    },
    clear () {
      this.selecting = false
      this.selected = []
    },
    createProduct () {
      this.$router.push({ name: 'create-product' })
    },
    editProduct (index) {
      this.$router.push({ name: 'edit-product', params: { menu: this.catalog[this.selected[0]] } })
    },
    deleteProduct () {
      this.selected.forEach(element => {
        this.catalog = this.catalog.filter(x => x.name !== element)
      })
    },
    goToDetails () {
      if (!this.selecting) {
        this.$router.push({ name: 'create-product' })
      }
    }
  }
}
</script>

<style lang="scss">
.catalog .v-list-item__subtitle {
  white-space: normal !important;
}
.f-12 {
  font-size: 13px !important;
}
.f-13 {
  font-size: 13px !important;
}
.square-avatar {
  border-radius: 0 !important;
}
span.v-ripple_container {
  display: none !important;
}
</style>

<style lang="scss">
.v-input.v-input--switch {
  .v-input__slot {
    place-content: center !important;
  }
}
</style>
