<template>
  <v-card
    max-width="100%"
    height="100%"
  >
    <v-toolbar
      color="pink"
      dark
      class="px-4"
      tile
      style="position: fixed; z-index: 8; width: 100%;"
      v-if="!selected.length"
    >
      <v-app-bar-nav-icon @click.stop="showDrawer"></v-app-bar-nav-icon>

      <v-toolbar-title>Proveedores</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon rounded @click="createProvider">
        <v-icon class="mr-2">mdi-plus</v-icon>
      </v-btn>
    </v-toolbar>

    <v-toolbar
      color="pink"
      dark
      class="px-4"
      tile
      style="position: fixed; z-index: 8; width: 100%;"
      v-if="selected.length"
    >
      <v-btn icon @click="clear" v-if="selected.length >= 1">
        <v-icon>mdi-close</v-icon>
      </v-btn>

      <v-toolbar-title class="pl-0">{{ `${selected.length} seleccionados`}}</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn class="ml-2 pr-0" text v-if="selected.length == 1" @click="editProvider()">
        <v-icon>mdi-pencil</v-icon>
      </v-btn>
      <v-btn class="px-0" text v-if="selected.length == 1" @click="deleteProvider">
        <v-icon>mdi-delete</v-icon>
      </v-btn>

      <v-btn text v-if="selected.length > 1" @click="deleteProvider">
        Borrar
        <v-icon class="ml-2">mdi-delete</v-icon>
      </v-btn>
    </v-toolbar>

    <v-list two-line class="mt-12 providers">
      <v-list-item-group
        v-model="selected"
        active-class="pink--text"
        multiple
        @change="clearSelection"
      >
        <template v-for="(item, index) in providers">
          <v-list-item :key="index" :value="item.id" :ripple="false"
            @mousedown="start(1)"
            @mouseleave="stop"
            @mouseup="stop"
            @touchstart="start(1)"
            @touchend="stop"
            @touchcancel="stop">
            <template v-slot:default>
              <v-list-item-content @click="editProvider(item.id)">
                <v-list-item-title v-text="item.name"></v-list-item-title>
                <v-list-item-subtitle v-text="item.phone"></v-list-item-subtitle>
              </v-list-item-content>
            </template>
          </v-list-item>

          <v-divider
            :key="'divider_'+index"
          ></v-divider>
        </template>
      </v-list-item-group>
    </v-list>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    selected: [],
    selecting: false,
    interval: false,
    count: 0,
    providers: []
  }),
  created () {
    this.providers = this.$store.getters.providers()
  },
  watch: {
    selected (newValue, oldValue) {
      if (newValue.length === 0) {
        this.selecting = false
      }
    }
  },
  methods: {
    showDrawer () {
      this.$store.dispatch('showDrawer', !this.$store.getters.showDrawer())
    },
    start () {
      this.interval = setInterval(() => {
        this.selecting = true
        return this.selecting
      }, 350)
    },
    stop () {
      clearInterval(this.interval)
      this.interval = false
    },
    clearSelection () {
      if (!this.selecting) {
        this.selected = []
      }
    },
    clear () {
      this.selecting = false
      this.selected = []
    },
    createProvider () {
      this.$router.push({ name: 'create-provider', params: {} })
    },
    editProvider (id) {
      if (!this.selecting || (this.selecting && this.selected.length === 1)) {
        const providerId = id || this.selected[0]
        this.$router.push({ name: 'edit-provider', params: { provider: this.providers.find(x => x.id === providerId) } })
      }
    },
    deleteProvider () {
      this.selected.forEach(providerId => {
        this.providers = this.providers.filter(x => x.id !== providerId)
      })
    }
  }
}
</script>

<style lang="scss">
.providers .v-list-item__subtitle {
  white-space: normal !important;
}
</style>
